export  const xmlLayerCtrl = {}



function createXMLDOM() {
  var xmlDOM;
  if (window.ActiveXObject) {
    xmlDOM = new ActiveXObject('Microsoft.XMLDOM');
  } else if (document.implementation
    && document.implementation.createDocument) {
    xmlDOM = document.implementation.createDocument('', '', null);
  } else {
    alert('您的浏览器不支持文档对象XMLDOM');
    return;
  }
  return xmlDOM;
}



xmlLayerCtrl.createXML = function (layerArr) {
  var xmlDOM = createXMLDOM();

  if (xmlDOM) {
    let layersNode = xmlDOM.createElement('LAYERS');
    for (let item of layerArr) {


      let layerNode = xmlDOM.createElement('LAYER');

      layerNode.setAttribute('SERVICES_ADDR', item.SERVICES_ADDR);
      layerNode.setAttribute('LAYERID', item.LAYERID);
      layerNode.setAttribute('MAPSERVICES', item.MAPSERVICES);
      layerNode.setAttribute('LAYER_TYPE', item.LAYER_TYPE);
        layerNode.setAttribute('DISPLAYLAYERNAME', item.DISPLAYLAYERNAME);

      let SELDEFINENode = xmlDOM.createElement('SELDEFINE');
      for(let selNode of item.SELDEFINE.PROPERTY){
        let PROPERTYNode = xmlDOM.createElement('PROPERTY');
        PROPERTYNode.setAttribute('PROPER_KEY', selNode.PROPER_KEY);
        PROPERTYNode.setAttribute('PROPER_VALUE', selNode.PROPER_VALUE);
        SELDEFINENode.appendChild(PROPERTYNode);
      }
      layerNode.appendChild(SELDEFINENode);

      let MOUSEMOVE_FIELDNode = xmlDOM.createElement('MOUSEMOVE_FIELD');
      for(let selNode of item.MOUSEMOVE_FIELD.FIELD){
        let fieldNode = xmlDOM.createElement('FIELD');
        fieldNode.setAttribute('FIELD_CNAME', selNode.FIELD_CNAME);
        fieldNode.setAttribute('FIELD_ENAME', selNode.FIELD_ENAME);
        MOUSEMOVE_FIELDNode.appendChild(fieldNode);
      }
      layerNode.appendChild(MOUSEMOVE_FIELDNode);


      layersNode.appendChild(layerNode);

    }
    xmlDOM.appendChild(layersNode);

    return parserXMLToString(xmlDOM);
  }

}

function parserXMLToString(xmlDOM) {
  if (window.ActiveXObject) {
    return xmlDOM.xml;
  } else if (document.implementation
    && document.implementation.createDocument) {
    return new XMLSerializer().serializeToString(xmlDOM);
  }
}







