<template>
    <div style="height: 100%;width: 100%" class="row">
      <div class="col-md-6">
        <div class="col-md-6">
            <RadioGroup v-model="layerType">
              <Radio label="单个"></Radio>
              <Radio label="批量"></Radio>
            </RadioGroup>
        </div>
        <div class="col-md-6">

          <RadioGroup v-model="xmlType">
            <Radio label="xml"></Radio>
            <Radio label="json"></Radio>
          </RadioGroup>
        </div>


        <Form v-show="layerType == '单个'" :model="formItem" :label-width="80">
          <FormItem label="SERVICES地址">
            <Input @on-change="serviceChane" v-model="formItem.SERVICES_ADDR" ></Input>
          </FormItem>
          <FormItem label="SERVICESID">
            <Input v-model="formItem.LAYERID" ></Input>
          </FormItem>
          <FormItem label="图层名称">
            <Input v-model="formItem.DISPLAYLAYERNAME"></Input>
          </FormItem>
          <FormItem label="MAPSERVICE地址">
            <Input v-model="formItem.MAPSERVICES" ></Input>
          </FormItem>
          <FormItem label="图层类型">
            <Select v-model="formItem.LAYER_TYPE">
              <Option value="ArcGISDynamicMapServiceLayer">ArcGISDynamicMapServiceLayer</Option>
              <Option value="ArcGISTiledMapServiceLayer">ArcGISTiledMapServiceLayer</Option>
              <Option value="ArcGISImageServiceLayer">ArcGISImageServiceLayer</Option>
            </Select>
          </FormItem>

        </Form>

        <Form   v-show="layerType == '批量'" :label-width="80">
          <!--<FormItem label="是否需要替换变量">-->
            <!--<Input v-model="urlParameter" ></Input>-->
          <!--</FormItem>-->
          <FormItem label="MAPSERVICE地址">
            <Input v-model="MAPSERVICES" ></Input>
          </FormItem>
        </Form>

        <Button  @click="btnClick" type="primary">生成配置</Button>
        <Button  @click="clearClick" type="error">清空</Button>
      </div>
      <div style="height: 100%" class="col-md-6">
        <Input class="t-textarea" :rows="35"  v-model="resultText"   type="textarea"></Input>
      </div>
      <div v-show="false" id="mapLayer">
      </div>
    </div>
</template>

<script>

    import {mapLayerCtrl}  from  './mapLayerCtrl';
    import {xmlLayerCtrl} from './xmlLayerCtrl';
    export default {
        components: {

        },
        data(){
            return {
              start:"-1",
              end:"-1",
              layerType:'批量',
              xmlType:'json',
              phone: 'apple',
              urlParameter:"",
              MAPSERVICES:"",
              resultText:"",
              formItem:{
                "SERVICES_ADDR": "",
                "LAYERID": "",
                "DISPLAYLAYERNAME": "",
                "MAPSERVICES": "",
                "LAYER_TYPE": "",
              }
            }
        },
        props: {},
        methods: {
            btnClick(){
                if(this.layerType == '单个'){
                  if(this.formItem.LAYER_TYPE == 'ArcGISDynamicMapServiceLayer'){
                    mapLayerCtrl.getSingleLayer(this.formItem.MAPSERVICES,this.formItem.LAYERID,this.addMoreLayerFun);
                  }else{
                    if(this.xmlType == "json"){
                      let newItem = JSON.stringify(mapLayerCtrl.getSingleLayerOther(this.formItem),null, 2);
                      newItem = newItem.substr(1,newItem.length - 2);
                      this.resultText = newItem;
                    }else{
                      let newItem = xmlLayerCtrl.createXML(mapLayerCtrl.getSingleLayerOther(this.formItem));
                      this.resultText = newItem;
                    }

                  }

                }else{
                    mapLayerCtrl.getBatchLayer(this.MAPSERVICES,this.addMoreLayerFun);
                }

            },
            addMoreLayerFun(layers){
              if(this.xmlType == "xml"){
                let newItem = xmlLayerCtrl.createXML(layers);
                this.resultText = newItem;

              }else{
                let newItem = JSON.stringify(layers,null, 2);
                newItem = newItem.substr(1,newItem.length - 2);
                this.resultText = newItem;
              }


           },
           serviceChane(){
                if(this.formItem.SERVICES_ADDR.indexOf('MapServer') != -1){
                  let arr  = this.formItem.SERVICES_ADDR.split('MapServer');
                  this.formItem.LAYERID = arr[1].split('/')[1];
                  this.formItem.MAPSERVICES = arr[0] + 'MapServer';
                  this.formItem.LAYER_TYPE = "ArcGISDynamicMapServiceLayer";
                  mapLayerCtrl.getSingleLayerName(this.formItem.MAPSERVICES, this.formItem.LAYERID,this.getLayerName);
                  console.log(arr);

                }

          },
          getLayerName(layerName){
               this.formItem.DISPLAYLAYERNAME = layerName;
          },
          clearClick(){
            this.formItem.LAYERID = "";
            this.formItem.MAPSERVICES = "";
            this.formItem.LAYER_TYPE = "";
            this.formItem.DISPLAYLAYERNAME = "";
            this.formItem.SERVICES_ADDR = "";
          }

        },
        computed: {},
        mounted(){
          mapLayerCtrl.init();
        },
        destroyed(){

        }
    }
</script>

<style  scoped>


  #mapLayer{
    width: 0px;
    height: 0px;
  }
</style>



