<template>
  <div style="height: 100%;width: 100%" class="row">
    <div class="col-md-6">
      <Form :label-width="80">
        <FormItem label="SERVICES地址">
          <Input v-model="formItem.SERVICES_ADDR"></Input>
        </FormItem>
        <FormItem label="起点">
          <Input v-model="formItem.start"></Input>
        </FormItem>
        <FormItem label="终点">
          <Input v-model="formItem.end"></Input>
        </FormItem>
        <FormItem label="间隔数">
          <Input v-model="formItem.num"></Input>
        </FormItem>
        <FormItem label="proj">
          <Input v-model="formItem.proj"></Input>
        </FormItem>
        <FormItem label="进度">
          <Input disabled v-model="formItem.count"></Input>
        </FormItem>
        <Button @click="btnClick" type="primary">生成配置</Button>
      </Form>
    </div>
    <div style="height: 100%" class="col-md-6">
      <div ref="test" class="t-textarea" style="width: 500px;max-height: 800px;background-color: #cccccc;overflow: auto">

      </div>
    </div>
    <div v-show="false" id="mapLayer">
    </div>
  </div>


</template>

<script>

  import {shpGetCtrl} from "./shpGetCtrl"


  export default {
    components: {},
    data(){
      return {
        textModel: "",
        formItem: {
          SERVICES_ADDR: "http://121.201.65.133:2001/arcgis/rest/services/baoan_data5/MapServer/8",
          start: 0,
          end: -1,
          num: 50,
          proj:"",
          count: 0,
        }
      }
    },
    props: {},
    methods: {
      btnClick(){
        this.textModel = "";
        this.formItem.count = 0;
        let arr = new Array();
        shpGetCtrl.getShpJson(this.formItem, arr, this.getJson);
      },
      getJson(arr){

        for (let i = 0; i < arr.length; i++) {

          if (i == 0) {

            this.textModel = JSON.stringify(arr[i]);
            this.textModel = this.textModel.substr(0,  this.textModel.length - 19);


          } else {
            let str  = JSON.stringify(arr[i].features);
            this.textModel =  this.textModel +  "," + str.substr(1, str.length - 2);

          }
        }

        this.textModel  =  this.textModel  + "]}";
        ipcRenderer.send('asynchronous-message', this.textModel );
//        console.log( this.textModel );

      }
    },
    computed: {},
    mounted(){
      shpGetCtrl.init();
    },
    destroyed(){

    }
  }
</script>

<style scoped>
  #mapLayer {
    width: 0px;
    height: 0px;
  }

</style>



