import * as esriLoader from 'esri-loader';

export const shpGetCtrl = {};





shpGetCtrl.init = function (url,callBackFun) {
  try{
    if (!esriLoader.isLoaded()) {
      // no, lazy load it the ArcGIS API before using its classes
      esriLoader.bootstrap((err) => {
        if (err) {
//                            console.error(err);
        }
        // once it's loaded, create the map

      }, {
        // use a specific version instead of latest 4.x
        url:layerConfig.arcgisAPIUrl

      });
    } else {
      // ArcGIS API is already loaded, just create the map

    }
  }
  catch (e){

  }
}


shpGetCtrl.getShpJson = function (layerConfig,arr,callBackFun) {

  esriLoader.dojoRequire(['esri/map',
      "esri/tasks/query",
      "esri/graphic",
      "esri/graphicsUtils",
      "esri/tasks/QueryTask",
      "esri/geometry/Point",
      "esri/geometry/Polyline",
      "esri/geometry/Polygon",

    ],
    (Map,
     Query,
     Graphic,
     graphicsUtils,
     QueryTask,
     Point,
     Polyline,
     Polygon
    ) => {
      let queryTask = new QueryTask(layerConfig.SERVICES_ADDR);
      let query  = new Query();
      let  end  = Number(layerConfig.count) + Number(layerConfig.num);
      if(layerConfig.end < end && layerConfig.end != -1){
        end = layerConfig.end;
      }
      query.where  = "OBJECTID >= " + layerConfig.count + " and  OBJECTID < " + end;
      query.returnGeometry = true;
      query.outFields = ['*'];
      queryTask.execute(query,(idResults) => {

        if(idResults.features.length > 0){

          for(let item of idResults.features){
            delete item.geometry.spatialReference;
            delete item.geometry._ring;
          }
          if(arr.length == 0){
            if(layerConfig.proj = "null"){
              idResults.spatialReference = null;

            }
          }
          arr.push(idResults);
          layerConfig.count = Number(layerConfig.count) +  Number(layerConfig.num);
          shpGetCtrl.getShpJson(layerConfig,arr,callBackFun);
        }else{
          callBackFun(arr);
        }



      })
    });
}



