
import * as esriLoader from 'esri-loader';

export const mapLayerCtrl = {}


mapLayerCtrl.init = function (url,callBackFun) {
    try{
        if (!esriLoader.isLoaded()) {
            // no, lazy load it the ArcGIS API before using its classes
            esriLoader.bootstrap((err) => {
                if (err) {
//                            console.error(err);
                }
                // once it's loaded, create the map

            }, {
                // use a specific version instead of latest 4.x
                url: layerConfig.arcgisAPIUrl

            });
        } else {
            // ArcGIS API is already loaded, just create the map

        }
    }
    catch (e){

    }
}

mapLayerCtrl.map = {};

mapLayerCtrl.layerLength = 0;

mapLayerCtrl.layerConfigs = [];


mapLayerCtrl.getSingleLayerName = function (url,id,callBackFun) {
  esriLoader.dojoRequire(['esri/map',
      'esri/layers/ArcGISTiledMapServiceLayer',
      'esri/layers/ArcGISDynamicMapServiceLayer',
      'esri/layers/ArcGISImageServiceLayer',
      "esri/dijit/OverviewMap",
      "esri/layers/WMTSLayer",
      "esri/layers/WMTSLayerInfo",
      "esri/geometry/Extent",
      "esri/layers/TileInfo",
      "esri/SpatialReference",
    ],
    (Map,
     ArcGISTiledMapServiceLayer,
     ArcGISDynamicMapServiceLayer,
     ArcGISImageServiceLayer,
     OverviewMap,
     WMTSLayer,
     WMTSLayerInfo,
     Extent,
     TileInfo,
     SpatialReference
    ) => {

      mapLayerCtrl.layerLength = 0;
      mapLayerCtrl.layerConfigs = [];
      let map = new Map("mapLayer");
      let dynamic = new ArcGISDynamicMapServiceLayer(url);
      map.addLayer(dynamic);
      map.on('load',loadComplete);
      function loadComplete() {
        if(dynamic.layerInfos.length > 0){
          for(let layerObj of dynamic.layerInfos){
            if(layerObj.subLayerIds == null){
              if(String(layerObj.id) === String(id))
              {
                console.log(layerObj)
                callBackFun(layerObj.name);
              }

            }
          }

        }
      }


    });
}

mapLayerCtrl.getSingleLayerOther = function (formItem) {

  mapLayerCtrl.layerLength = 0;
  mapLayerCtrl.layerConfigs = [];
  let layerConfig = new Object();
  layerConfig.SERVICES_ADDR = "";
  layerConfig.LAYERID = "";
  layerConfig.DISPLAYLAYERNAME = formItem.DISPLAYLAYERNAME;
  layerConfig.MAPSERVICES = formItem.MAPSERVICES;
  layerConfig.LAYER_TYPE = formItem.LAYER_TYPE
  layerConfig.MOUSEMOVE_FIELD = {};
  layerConfig.MOUSEMOVE_FIELD.FIELD = [];
  layerConfig.SELDEFINE = {};
  layerConfig.SELDEFINE.PROPERTY = [
    {
      "PROPER_DESCRIPTION":"权限值",
      "PROPER_KEY": "priority",
      "PROPER_VALUE": "2"
    },
    {
      "PROPER_DESCRIPTION":"点击查询",
      "PROPER_KEY": "clickFlag",
      "PROPER_VALUE": "1"
    },
    {
      "PROPER_DESCRIPTION":"移动查询",
      "PROPER_KEY": "queryFlag",
      "PROPER_VALUE": "1"
    },
    {
      "PROPER_DESCRIPTION":"透明度",
      "PROPER_KEY": "alpha",
      "PROPER_VALUE": "0.5"
    }
  ];
  mapLayerCtrl.layerConfigs.push(layerConfig);
  return mapLayerCtrl.layerConfigs;

}

mapLayerCtrl.getSingleLayer = function (url,id,callBackFun) {


  esriLoader.dojoRequire(['esri/map',
      'esri/layers/ArcGISTiledMapServiceLayer',
      'esri/layers/ArcGISDynamicMapServiceLayer',
      'esri/layers/ArcGISImageServiceLayer',
      "esri/dijit/OverviewMap",
      "esri/layers/WMTSLayer",
      "esri/layers/WMTSLayerInfo",
      "esri/geometry/Extent",
      "esri/layers/TileInfo",
      "esri/SpatialReference",
    ],
    (Map,
     ArcGISTiledMapServiceLayer,
     ArcGISDynamicMapServiceLayer,
     ArcGISImageServiceLayer,
     OverviewMap,
     WMTSLayer,
     WMTSLayerInfo,
     Extent,
     TileInfo,
     SpatialReference
    ) => {

      mapLayerCtrl.layerLength = 0;
      mapLayerCtrl.layerConfigs = [];
      let map = new Map("mapLayer");
      let dynamic = new ArcGISDynamicMapServiceLayer(url);
      map.addLayer(dynamic);
      map.on('load',loadComplete);
      function loadComplete() {
        if(dynamic.layerInfos.length > 0){
          for(let layerObj of dynamic.layerInfos){
            if(layerObj.subLayerIds == null){
              if(String(layerObj.id) === String(id))
              {
                mapLayerCtrl.getLayer2(layerObj,url,callBackFun);
              }

            }
          }

        }
      }


    });
}




mapLayerCtrl.getLayer2 = function (layerObj,url,callBackFun) {
  let layerConfig = new Object();
  layerConfig.SERVICES_ADDR = url + "/" + layerObj.id;
  layerConfig.LAYERID = String(layerObj.id);
  layerConfig.DISPLAYLAYERNAME = layerObj.name;
  layerConfig.MAPSERVICES = url;
  layerConfig.LAYER_TYPE = "ArcGISDynamicMapServiceLayer";
  layerConfig.MOUSEMOVE_FIELD = {};
  layerConfig.MOUSEMOVE_FIELD.FIELD = [];
  layerConfig.SELDEFINE = {};
  layerConfig.SELDEFINE.PROPERTY = [
    {
      "PROPER_DESCRIPTION":"权限值",
      "PROPER_KEY": "priority",
      "PROPER_VALUE": "5"
    },
    {
      "PROPER_DESCRIPTION":"点击查询",
      "PROPER_KEY": "clickFlag",
      "PROPER_VALUE": "1"
    },
    {
      "PROPER_DESCRIPTION":"移动查询",
      "PROPER_KEY": "queryFlag",
      "PROPER_VALUE": "1"
    },
    {
      "PROPER_DESCRIPTION":"透明度",
      "PROPER_KEY": "alpha",
      "PROPER_VALUE": "0.5"
    }
  ];
  mapLayerCtrl.layerConfigs.push(layerConfig);
  esriLoader.dojoRequire(['esri/map',
      "esri/tasks/query",
      "esri/graphic",
      "esri/graphicsUtils",
      "esri/tasks/QueryTask",
      "esri/geometry/Point",
      "esri/geometry/Polyline",
      "esri/geometry/Polygon",

    ],
    (Map,
     Query,
     Graphic,
     graphicsUtils,
     QueryTask,
     Point,
     Polyline,
     Polygon
    ) => {
      let queryTask = new QueryTask(layerConfig.SERVICES_ADDR);
      let query  = new Query();
      query.where  = "1=1";
      query.returnGeometry = false;
      query.outFields = ['*'];
      queryTask.execute(query,(idResults) => {

        console.log(idResults);
        let fields = idResults.fields;
        for(let item of fields){
          let fieldObj = new Object();
          fieldObj.FIELD_CNAME = item.alias;
          fieldObj.FIELD_ENAME = item.name;
          layerConfig.MOUSEMOVE_FIELD.FIELD.push(fieldObj);
        }

          callBackFun(mapLayerCtrl.layerConfigs);

      })
    });
}






mapLayerCtrl.getBatchLayer = function (url,callBackFun) {

    esriLoader.dojoRequire(['esri/map',
            'esri/layers/ArcGISTiledMapServiceLayer',
            'esri/layers/ArcGISDynamicMapServiceLayer',
            'esri/layers/ArcGISImageServiceLayer',
            "esri/dijit/OverviewMap",
            "esri/layers/WMTSLayer",
            "esri/layers/WMTSLayerInfo",
            "esri/geometry/Extent",
            "esri/layers/TileInfo",
            "esri/SpatialReference",
        ],
        (Map,
         ArcGISTiledMapServiceLayer,
         ArcGISDynamicMapServiceLayer,
         ArcGISImageServiceLayer,
         OverviewMap,
         WMTSLayer,
         WMTSLayerInfo,
         Extent,
         TileInfo,
         SpatialReference
        ) => {

            mapLayerCtrl.layerLength = 0;
            mapLayerCtrl.layerConfigs = [];
           let map = new Map("mapLayer");
           let dynamic = new ArcGISDynamicMapServiceLayer(url);
           map.addLayer(dynamic);
           map.on('load',loadComplete);
           function loadComplete() {
               if(dynamic.layerInfos.length > 0){
                   mapLayerCtrl.getLayer(dynamic.layerInfos,url,callBackFun);
               }
           }


        });
}





mapLayerCtrl.getLayer = function (layerArr,url,callBackFun) {

    let layerObj = layerArr[mapLayerCtrl.layerLength];
    if(layerObj.subLayerIds == null){
        let layerConfig = new Object();
        layerConfig.SERVICES_ADDR = url + "/" + layerObj.id;
        layerConfig.LAYERID = String(layerObj.id);
        layerConfig.DISPLAYLAYERNAME = layerObj.name;
        layerConfig.MAPSERVICES = url;
        layerConfig.LAYER_TYPE = "ArcGISDynamicMapServiceLayer";
        layerConfig.MOUSEMOVE_FIELD = {};
        layerConfig.MOUSEMOVE_FIELD.FIELD = [];
        layerConfig.SELDEFINE = {};
        layerConfig.SELDEFINE.PROPERTY = [
            {
                "PROPER_DESCRIPTION":"权限值",
                "PROPER_KEY": "priority",
                "PROPER_VALUE": "5"
            },
            {
                "PROPER_DESCRIPTION":"点击查询",
                "PROPER_KEY": "clickFlag",
                "PROPER_VALUE": "1"
            },
            {
                "PROPER_DESCRIPTION":"移动查询",
                "PROPER_KEY": "queryFlag",
                "PROPER_VALUE": "1"
            },
            {
                "PROPER_DESCRIPTION":"透明度",
                "PROPER_KEY": "alpha",
                "PROPER_VALUE": "0.5"
            }
        ];
        mapLayerCtrl.layerConfigs.push(layerConfig);
        esriLoader.dojoRequire(['esri/map',
                "esri/tasks/query",
                "esri/graphic",
                "esri/graphicsUtils",
                "esri/tasks/QueryTask",
                "esri/geometry/Point",
                "esri/geometry/Polyline",
                "esri/geometry/Polygon",

            ],
            (Map,
             Query,
             Graphic,
             graphicsUtils,
             QueryTask,
             Point,
             Polyline,
             Polygon
            ) => {
                let queryTask = new QueryTask(layerConfig.SERVICES_ADDR);
                let query  = new Query();
                query.where  = "1 > 2";
                query.returnGeometry = false;
                query.outFields = ['*'];
                queryTask.execute(query,(idResults) => {

                    console.log(layerConfig);
                    let fields = idResults.fields;
                    for(let item of fields){
                        let fieldObj = new Object();
                        fieldObj.FIELD_CNAME = item.alias;
                        fieldObj.FIELD_ENAME = item.name;
                        layerConfig.MOUSEMOVE_FIELD.FIELD.push(fieldObj);
                    }
                    if( mapLayerCtrl.layerLength + 1 == layerArr.length){
                        callBackFun(mapLayerCtrl.layerConfigs);
                    }else{
                        mapLayerCtrl.layerLength = mapLayerCtrl.layerLength + 1;
                        mapLayerCtrl.getLayer(layerArr,url,callBackFun);
                    }
                })
            });
    }else{
        mapLayerCtrl.layerLength = mapLayerCtrl.layerLength + 1;
        mapLayerCtrl.getLayer(layerArr,url,callBackFun);
    }


}

mapLayerCtrl.queryLayer = function () {

}






